* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.country-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 15px 10px  10px 15px;
  font-size: 30px;
  font-weight: 500;
  font-family: sans-serif;
}

.flag {
  width: 35px;
}

.flag + span {
  margin-left: 15px;
}

.info-field {
  margin-left: 15px;
  font-size: 20px;
  font-family: Helvetica, sans-serif;
  line-height: 1.75rem;
}

.info-field:last-child {
  margin-bottom: 20px;
}

.caption {
  font-weight: 600;
}